import './App.css';
import React from 'react';
import {Route, Switch, Link} from "react-router-dom";
import About from "./About";
import Home from "./Home";

function App() {
  return (
    <div className="App">
      <nav>
        <ul id="navigation">
          <li>
            <Link to={"/"}>Home</Link>
          </li>
          <li>
            <Link to={"/about"}>About</Link>
          </li>
          <li>
            <Link to={"/contact"}>Contact</Link>
          </li>
        </ul>
      </nav>
      <Switch>
        <Route exact={true} path={"/"}>
          <Home />
        </Route>
        <Route path={"/about"}>
          <About />
        </Route>
      </Switch>
    </div>
  );
}

export default App;
